.nav__heading {
    overflow: hidden;
    .nav__heading__content {
        display: flex;
        justify-content: flex-end;
        margin-top: 30px;

        .nav__tel {
            font-size: 14px;
            font-weight: 700;

            color: #fff;
            margin-right: 15px;

            .sdt {
                margin-left: 7px;

            }
        }

        .nav__search {
            font-size: 14px;
            color: #fff;
            padding-left: 15px;
            border-left: 1px dotted rgba(255, 255, 255, .5);
        }
    }

}

.nav__header {
    .nav__logo {
        img {
            max-width: 100%;
            height: auto;
        }
    }

}

#my-nav {
    display: block;
    text-align: right;
    ul {
        display: inline-block;
        border-bottom: 1px solid rgba(255,255,255,.2);
        li {
           display: inline-block;
            position: relative;
            
            a {
                font-size: 16px;
                font-weight: 400;
                line-height: .8em;
                display: inline-block;
                letter-spacing: 3px;
                text-transform: uppercase;
                color: #fff;
                padding: 20px 24px;
            }
            &:after{
                position: absolute;
                right: 0;
                bottom: 0;
                left: 0;
                display: block;
                z-index: 99;
                width: 0;
                height: 2px;
                content: '';
                transition: all .25s ease-in-out;
                background-image: linear-gradient(to right,#fbbd61,#ec7532);
                box-shadow: 0 0 20px 0 #fbbd61;
            }
            &:hover{
                &:after{
                    width: 100%;
                }
            }
        }
        .active{
            &:after{
                width: 100%;
            }
        }
       
    }
}

.navigation {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 99;

}