.coming__soon {
    position: relative;
    color: #ffffff;
    background: #101010;
    width: 100%;

    h2 {
        position: relative;
        font-size: 18px;
        margin-bottom: 40px;
        letter-spacing: 1.7px;
        text-transform: uppercase;
        color: #ec7532;
        width: 100%;
        padding-bottom: 20px;
        border-bottom: 1px solid #d8d8d8;

        &:after {
            position: absolute;
            left: 0;
            bottom: 0;
            right: auto;
            height: 3px;
            width: 125px;
            content: "";
            z-index: 99;
            background-image: linear-gradient(to right, #fbbd61, #ec7532);
        }
    }



    .comingsoon-slide {
        padding-top: 75px;

        div {
            position: static;
        }

        .single-slide {
            .bg {
                position: absolute;
                z-index: 0;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 100%;

            }

            .slide-content {
                span.title {
                    font-size: 14px;
                    letter-spacing: 2px;
                    text-transform: uppercase;
                    color: #ec7532;
                }

                h3 {
                    font-size: 34px;
                    padding: 0;
                    letter-spacing: 1px;
                }

                .star-rating {
                    display: inline-block;
                    margin-right: 8px;
                    position: relative;

                    i {
                        font-size: 14px;
                        width: 14px;
                        display: inline-block;
                        color: #fbbd61;
                    }
                }

                .date {
                    font-size: 14px;
                    display: inline-block;
                    opacity: .75;
                    color: #fff;

                    i {
                        font-size: 14px;
                        margin-right: 5px;
                        display: inline-flex;
                        vertical-align: middle;
                    }
                }

                p {
                    &:first-of-type {
                        padding: 20px 0 20px;
                    }

                    a {
                        font-size: 12px;
                        position: relative;
                        letter-spacing: 4px;
                        text-transform: uppercase;
                        color: #fbbd61;
                    }
                }
            }
        }

        .slide-video {
            .video {
                position: relative;
    
                i {
                    font-size: 50px;
                    line-height: 75px;
                    position: absolute;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    left: 0;
                    width: 80px;
                    height: 80px;
                    margin: auto;
                    transition: all 300ms ease-in-out;
                    text-align: center;
                    text-indent: 14px;
                    color: #fff;
                    border: 2px solid #ec7532;
                    border-radius: 50%;
                    display: inline-flex;
                    vertical-align: middle;
                    cursor: pointer;
                    z-index: 1;
                }

                img {
                    max-width: 100%;
                    height: auto;
                }
            }
        }
    }

    .slick-carousel {
        position: relative !important;
        padding: 55px 0;
        display: block;

        .slick-slide {
            margin: 0 15px;
            text-align: center;
        }

        img {
            max-width: 100%;
            height: auto;
            border-radius: 10px;
        }

        h5 {
            font-size: 16px;
            overflow: hidden;
            width: 100%;
            margin-top: 20px;
            margin-bottom: 0;
            text-align: center;
            white-space: nowrap;
            text-overflow: ellipsis;
        }

        .release-date {
            font-size: 14px;
            opacity: .5;
            color: #fff;
        }

        .slick-arrow {
            font-size: 40px;
            font-size: 40px;
            position: absolute;
            top: 35%;
            cursor: pointer;
            transition: all 300ms ease-in-out;
            background: -webkit-linear-gradient(#fbbd61, #ec7532);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            display: inline-block;

        }

        .left {
            left: -65px;
        }

        .right {
            right: -65px;
        }
    }
}

