footer {
    color: #ffffff;
    background: #101010;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 0;
    padding: 75px 0 0;

    .footer__title {
        font-size: 16px;
        margin: 0;
        letter-spacing: 2px;
        text-transform: uppercase;
        color: #ec7532;
        border: none;
        margin-bottom: 15px;
    }

    a {
        font-size: 13px;
        display: inline-block;
        padding: 3px 0;
        letter-spacing: 1.5px;
        color: #fff;
        text-transform: capitalize;
        ;

        &:hover {
            color: #ec7532;
        }

        i {
            text-align: center;
            width: 15px;
            margin-right: 10px;
            color: rgba(255, 255, 255, .3);
        }
    }

    .copyright {
        text-align: center;
        font-size: 14px;
        font-weight: 500;
        padding: 20px 0;
        margin-top: 50px;
        width: 100%;
    }
}