.help {
    padding: 50px 0;

    p {
        font-size: 23px;
        font-weight: 300;
        margin-bottom: 10px;
        text-align: center;
        letter-spacing: 1px;
        color: #4a4a4a;
    }

    .gradient-text {
        font-size: 46px;
        font-weight: 400;
        background: -webkit-linear-gradient(#fbbd61, #ec7532);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
}