#hero {
    position: relative;

    .carousel__item__content {
        position: absolute;
        align-items: center;
        z-index: 99;
        top: 35%;
        left: 14%;
        justify-content: center;

        h3.title {
            font-size: 14px;
            letter-spacing: 2px;
            text-transform: uppercase;
            color: #fbbd61;
            padding: 0;
        }

        h1 {
            font-size: 52px;
            font-weight: 300;
            animation: Heading .5s .5s forwards;
            letter-spacing: 3px;
            color: #fff;
            border: none;
            padding: 0;
        }

        p {
            font-size: 18px;
            color: #fff;
        }

        .buttons {
            margin-top: 20px;

            .cerfiticate {
                font-size: 13px;
                font-weight: 700;
                line-height: 48px;
                display: inline-block;
                width: 48px;
                height: 48px;
                margin-right: 8px;
                transform: translateY(2px);
                text-align: center;
                color: #fff;
                border: solid 2px #fff;
                border-radius: 50%;
                text-transform: uppercase;
            }

        }

        
    }
    .carousel-indicators__dots{
        position: absolute;
        z-index: 99;
        bottom: 50px;
        left: 15%;
        li{
            height: 10px;
            width: 10px;
            border: 2px solid #fff;
            background-color: transparent;
            display: inline-block;
            margin-right: 8px;
            border-radius: 50%;
            cursor: pointer;
        }
        .active{
            background-color: #fff;
        }
    }
    .scroll{
        position: absolute;
        z-index: 1;
        bottom: 0;
        left: 50%;
        margin-left: -45px;
    }
}