.tabs {
    .movie-tabs {
        margin-top: 40px;
        margin-bottom: 40px;
        padding-bottom: 40px;
        border-bottom: 1px solid #d8d8d8;

        a {
            img {
                width: 100%;
                display: block;
            }

            ;
        }

        .title {
            font-size: 14px;
            letter-spacing: 2px;
            text-transform: uppercase;
            color: #ec7532;
        }

        h3 {
            font-size: 24px;
            font-weight: 300;
            letter-spacing: 1px;
            color: #101010;
            padding: 0;
        }

        p {
            margin: 0 0 20px;

            .arrow-button {
                font-size: 12px;
                position: relative;
                letter-spacing: 4px;
                text-transform: uppercase;
                color: #ec7532;
            }
        }

        .viewing-times {
            font-size: 12px;
            letter-spacing: 1.5px;
            text-transform: uppercase;
            color: #4a4a4a;
            i{
                font-size: 13px;
            }
        }
        .time{
            font-size: 14px;
            display: inline-block;
            margin-left: 5px;
            padding: 7px 10px;
            letter-spacing: 2px;
            color: #717171;
            border-radius: 5px;
            background-color: #d8d8d8;
        }
        .past{
            opacity: 0.5;
        }
        .running-time{
            font-size: 12px;
            text-align: right;
            letter-spacing: 1.5px;
            text-transform: uppercase;
            .certificate{
                font-size: 13px;
                font-weight: 700;
                line-height: 33px;
                display: inline-block;
                width: 33px;
                height: 33px;
                margin-left: 5px;
                text-align: center;
                letter-spacing: 0;
                color: #fff;
                border-radius: 50%;
                background: #4a4a4a;
            }
        }
        .space-10{
            display: none;
        }
    }

    ul {
        border-bottom: 1px solid #d8d8d8;

        li {
            display: inline-block;

            a {
                font-size: 18px;
                position: relative;
                display: block;
                padding: 10px 30px 15px;
                letter-spacing: 1px;
                text-transform: uppercase;
                color: #4a4a4a;

                &:after {

                    position: absolute;
                    left: 0;
                    bottom: 0;
                    width: 0;
                    height: 3px;
                    background-image: linear-gradient(to right, #fbbd61, #ec7532);
                    z-index: 100;
                    content: "";
                    display: block;

                }

            }
        }

        .ui-state-active {
            a {
                color: #ec7532;

                &:after {
                    width: 100%;
                }
            }


        }


    }
}