.newin {
    h2 {
        position: relative;
        font-size: 18px;
        margin-bottom: 40px;
        letter-spacing: 1.7px;
        text-transform: uppercase;
        color: #ec7532;
        width: 100%;
        padding-bottom: 20px;
        border-bottom: 1px solid #d8d8d8;

        &:after {
            position: absolute;
            left: 0;
            bottom: 0;
            right: auto;
            height: 3px;
            width: 125px;
            content: "";
            z-index: 99;
            background-image: linear-gradient(to right, #fbbd61, #ec7532);
        }
    }

    .slick-carousel {
        position: relative;
        .slick-dots{
            display: none !important;
        }
        .movie-slide {
            margin: 0 15px;
            text-align: center;
            .movie-poster {
                position: relative;
                overflow: hidden;
                margin-bottom: 20px;
                border-radius: 10px;
                &:hover{
                    &:before{
                        width: 200%;
                    }
                    aside{
                        opacity: 1;
                    }
                }
                &:before {
                    position: absolute;
                    z-index: 1;
                    bottom: 0;
                    left: -100px;
                    width: 0;
                    height: 100%;
                    content: '';
                    -webkit-transition: .5s;
                    transition: .5s;
                    -webkit-transform: skewX(30deg);
                    -ms-transform: skewX(30deg);
                    transform: skewX(30deg);
                    opacity: .9;
                    background-image: linear-gradient(to bottom, #fbbd61, #ec7532);
                }

                aside {
                    position: absolute;
                    z-index: 1;
                    display: flex;
                    width: 100%;
                    height: 100%;
                    -webkit-transition: .3s;
                    transition: .3s;
                    -webkit-transition-delay: .1s;
                    transition-delay: .1s;
                    transform: scale(.9);
                    text-align: center;
                    opacity: 0;
                    align-items: center;

                    div {
                        width: 100%;
                        .read-more{
                            font-size: 13px;
                            display: block;
                            margin-bottom: 10px;
                            cursor: pointer;
                            letter-spacing: 4px;
                            text-transform: uppercase;
                            color: #fff;
                        }
                        .date{
                            font-size: 16px;
                            display: block;
                            color: #fff;
                        }
                        .play {
                            font-size: 32px;
                            line-height: 70px;
                            display: block;
                            width: 80px;
                            height: 80px;
                            margin: 0 auto 20px;
                            color: #fff;
                            border: solid 2px #fff;
                            border-radius: 50%;

                            i {
                                display: inline-flex;
                                vertical-align: middle;
                            }
                        }
                    }

                }

                img {
                    height: auto;
                    width: 100%;
                }
            }
            .star-rating{
                display: inline-block;
                i{
                    font-size: 18px;
                    display: inline-block;
                    width: 18px;
                    color: #fbbd61;
                }
                .grey{
                    color: #d8d8d8;
                }
            }
        }
        .slick-prev {
            
            position: absolute;
            top: 30%;
            left: -65px;
            background: none;
            border: none;
            cursor: pointer;
            -webkit-transition: all 300ms ease-in-out;
            -moz-transition: all 300ms ease-in-out;
            transition: all 300ms ease-in-out;
            background: -webkit-linear-gradient(#fbbd61,#ec7532);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            i{
                font-size: 40px;
            }
        }
        .slick-next{
            position: absolute;
            top: 30%;
            background: none;
            border: none;
            right: -65px;
            cursor: pointer;
            -webkit-transition: all 300ms ease-in-out;
            -moz-transition: all 300ms ease-in-out;
            transition: all 300ms ease-in-out;
            background: -webkit-linear-gradient(#fbbd61,#ec7532);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            i{
                font-size: 40px;
            }
        }
      
    }
}