* {
    margin: 0;
    padding: 0;
}

body {
    font-family: roboto condensed, sans-serif;
    font-size: 16px;
    height: 100%;
    color: #717171;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, .004);
}

p,
ul {
    margin-bottom: 0;
}

ul {
    list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    line-height: 1.4em;
    position: relative;
    margin: 0 0 10px;
  
}

a {
    text-decoration: none !important;
    outline: none;
}

.btn {
    font-size: 13px;
    font-weight: 400;
    position: relative;
    overflow: hidden;
    width: auto;
    padding: 12px 24px;
    letter-spacing: 4px;
    text-transform: uppercase;
    color: #fff;
    border-width: 2px;
    border-radius: 30px;
}
.btn i{
    position: relative;
    z-index: 1;
    display: inline-flex;
    vertical-align: middle;
    color: #fff;
}
.btn span{
    position: relative;
    z-index: 1;
    color: #fff;
}
.btn-default {
    border: none;
    background-image: linear-gradient(to right, #fbbd61, #ec7532);
}
.section {
    padding-top: 75px;
    padding-bottom: 75px;
}



